.card {
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  box-shadow: 0 4px 8px rgb(0 0 0 / 20%);
}
.header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 0.5rem;
}
.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #000;
}
.title {
  margin: 0;
  font-family: "Tschick Bold", sans-serif;
  text-transform: uppercase;
}
.time {
  font-size: 0.75rem;
}
.type {
  margin-left: auto;
  background: #000;
  color: #fff;
  font-size: 0.75rem;
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
}
.body {
  margin-bottom: 0.5rem;
}
.readMore {
  background: none;
  border: none;
  color: inherit;
  cursor: pointer;
  padding: 0;
  text-decoration: underline;
}
.media {
  width: 100%;
  margin-top: 0.5rem;
  border-radius: 4px;
}
